import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { CepUF } from '../../../../models/dtos/ger/cepUF';
import { CLASS_NAME, IndiceParticipacaoMunicipio } from '../../../../models/dtos/mlf/indiceParticipacaoMunicipio';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const IndiceParticipacaoMunicipioSearchView: FC<SearchViewProps<IndiceParticipacaoMunicipio>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<IndiceParticipacaoMunicipio>({ mode: 'onBlur' });

  const getSearchExpression = () => {
    const expressions: SearchExpression[] = [];

    const cepUf: CepUF = formProps.getValues().cepUf;

    if (cepUf) {
      expressions.push({
        fieldName: 'cepUf.codigo',
        value: cepUf.codigo,
      });
    }

    return expressions;
  };

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção do Índice de Participação do Município"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
    >
      <CepUFIS id="cepUf" formProps={formProps} label="UF" isPrependLabel={true} placeholder="Filtrar UF" />

      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        cellEditProps={cellEditProps}
        dtoClassName={CLASS_NAME}
        searchExpressions={getSearchExpression}
        extraData={getExtraData(props, 'IndiceParticipacaoMunicipioEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default IndiceParticipacaoMunicipioSearchView;
